
#toolbar{
  float:left;
  width:80px;
  height:100%;
  background-color:#30414D;
  color:#767676;
  padding:35px 25px 25px 25px;
}
#toolbar i{
  font-size:30px;
  margin-bottom:35px;
  cursor:pointer;
  opacity:0.8;
  transition:opacity 0.5s ease;
}
#toolbar i:hover{
  opacity:1;
}
#toolbar i:active{
  color:#ffffff;
}
.starred{
  color:#F7AE4F;
}



