
#note-list{
  float:left;
  width:300px;
  height:100%;
  background-color:#F5F5F5;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}
#note-title{
  padding:5px 25px 25px 25px;
}
#note-title h2{
  font-weight:300;
  text-transform:uppercase;
  text-align:center;
  font-size:22px;
  padding-bottom:8px;
}
#note-list .container{
  height:calc(100%-137px);
  max-height:calc(100%-137px);
  overflow:auto;
  width:100%;
  padding:0;
}
#note-list .container .list-group-item{
  border:0;
  border-radius:0;
}
.list-group-item-heading{
  overflow: hidden;
  white-space: nowrap;
  margin:0;
  line-height: 21px;
  font-weight:300;
  font-size:15px;
  min-height:21px;
}



