
#note-editor{
  margin-left:380px;
  padding-left: 20px;
  height:100%;
}
#note-editor textarea{
  height:100%;
  border:0;
  border-radius: 0;
  box-shadow: 0 0 ;
  font-size:20px;
}
.editorarea::first-line{
  font-size:24px;
  font-weight:bold;
  line-height: 48px;
}



